<template>
  <v-container fluid>

      <router-link to="/add-new-runner">
         <v-btn fab dark absolute right bottom color="blue mb-4">
          <v-icon dark>add</v-icon>
        </v-btn>
      </router-link> 
     
     <v-layout row wrap>
    
          <v-flex xs4 v-for="category in race_category" :key="category.name">
            <v-card class="mr-1">
              <v-toolbar :color="category.color">
                <v-toolbar-title>{{category.name}}</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-btn icon>
                  <v-icon>accessible_forward</v-icon>
                </v-btn>
              </v-toolbar>
              
              
               <v-list>
                <v-list-tile v-for="item in participants" :key="item.id" avatar @click="showDetails(item.id)" v-if="item.race_category == category.name">
                 
                      <v-avatar :color="item.gender == 'female' ? 'pink' : 'black'" size="30px">
                        <span class="white--text">{{item.last_name.charAt(0).toUpperCase() }}</span>
                      </v-avatar>
                    
                      <v-list-tile-content class="pl-1">
                        <v-list-tile-title v-text="`${item.last_name}  ${item.first_name}`"></v-list-tile-title>
                      </v-list-tile-content>
                  
                       <v-list-tile-action class="text-xs-right">
                        <v-chip label :color="item.gender == 'female' ? 'pink' : 'black'" text-color="white">{{item.bib_number}}</v-chip>
                      </v-list-tile-action>
                   

                  </v-list-tile>
                 </v-list>
            </v-card>
          </v-flex>

    </v-layout>
  </v-container>
</template>

<script>
  import { db } from '../main'
  export default {
    data () {
      return {
        participants: [],
        race_category: [
           {
             name: '11KM', 
             color: 'teal'
           },
           {
            name: '21KM', 
            color: 'white'
           },
           {
             name: '50KM', 
             color: 'yellow'
           }
       ],
      }
    },
    firestore () {
      return {
        participants: db.collection('participants').orderBy('last_name')
      }
    },
    methods:{
       showDetails(id){
           this.$router.push('/'+id)
       }
    }

    
  }
</script>


